
//-webkit-overflow-scrolling: touch;
//animation-play-state: paused !important;
.wrapper {
  width: 400px;
  height: 100%;
  max-height: 90%;
  background-color: white;
  padding: 40px 30px;
  border-radius: 15px;
  overflow: hidden;
  @media (max-width: 1500px) {
    padding: 20px 30px;
    width: 350px;
  }
  @media (max-width: 900px) {
    padding: 15px 15px;
    width: 250px;
  }
}

.container {
  display: flex;
  flex-direction: column;
}

.title {
  text-align: center;
  margin-bottom: 40px;
  @media (max-width: 900px) {
    margin-bottom: 20px;
  }
}

h1 {
  font-family: Bahnschrift;
  font-size: 30px;
  font-weight: 700;
  margin: 0 0 25px 0;
  @media (max-width: 1500px) {
    margin: 0 0 15px 0;
    font-size: 25px;
  }
  @media (max-width: 900px) {
    margin: 0 0 10px 0;
    font-size: 20px;
  }

}

h3 {
  font-family: Bahnschrift;
  font-size: 25px;
  font-weight: 600;
  @media (max-width: 1500px) {
    font-size: 22px;
  }
  @media (max-width: 900px) {
    font-size: 18px;
  }
}

.stylesForTheform {
  margin-bottom: 50px;
  @media (max-width: 800px) {
    margin-bottom: 30px;
  }
  @media (max-width: 900px) {
    margin-bottom: 20px;
  }
}

label {
  color: #a5a5a5;
  font-size: 13px;
  @media (max-width: 1500px) {
    font-size: 12px;
  }
  @media (max-width: 900px) {
    font-size: 10px;
  }
}

.emailFieldItems {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  @media (max-width: 900px) {
    margin-bottom: 20px;
  }
}

.emailField {
  border-bottom: 1px solid black;
  padding: 5px 0;
  font-size: 20px;
  display: block;
  @media (max-width: 1500px) {
    padding: 0;
    font-size: 18px;
  }
}

.passFieldItems {
  display: flex;
  flex-direction: column;
}

.passField {
  border-bottom: 1px solid black;
  padding: 5px 0;
  font-size: 20px;
  display: block;
  @media (max-width: 1500px) {
    padding: 0;
    font-size: 18px;
  }
}

.registrationConfirmPasswordError {
  color: red;
}

.errorTextFromResponse {
  color: red;
}

.svg {
  position: relative;
  bottom: 35px;
  left: 91%;
  width: 20px;
  height: 20px;
  @media (max-width: 900px) {
    width: 15px;
    height: 15px;
    bottom: 30px;
  }
}

.footer {
  display: flex;
  justify-content: space-between;
}


.registerCancelButton {
  margin-bottom: 20px;
  background-color: #D7D8EF;
  border-radius: 20px;
  max-width: 45%;
  width: 100%;
  padding: 10px;
  color: #21268F;
  font-size: 16px;
  transition: all 0.3s ease;
  display: block;

  &:hover {
    box-shadow: 2px 2px 2px 2px #5b5dac;
    background-color: #21268F;
    color: white;
    max-width: 50%;
  }

  &:active {
    box-shadow: 1px 1px 1px 1px #6e70a1;
  }

  @media (max-width: 900px) {
    margin-bottom: 15px;
    font-size: 12px;
  }
}

